<template>
	<section id="gold">
    <span class="title">{{$t('gold.gold')}}</span>
    <el-col :span="24" style="padding-bottom: 0px" class="toolbar">
      <el-form :inline="true" :model="userForm" ref="userForm">
      	<el-form-item :label="$t('state')">
      		<el-select v-model="userForm.state" :placeholder="$t('SelectState')">
		      <el-option :label="$t('applying')" value="0"></el-option>
          <el-option :label="$t('toExtract')" value="1"></el-option>
		      <el-option :label="$t('outOnLoan')" value="2"></el-option>
		      <el-option label="提取失败" value="4"></el-option>
		    </el-select>
      	</el-form-item>
        <el-form-item :label="$t('SD')">
            <el-date-picker type="date" :placeholder="$t('CD')" v-model="userForm.startTime"></el-date-picker>              
        </el-form-item>
        <el-form-item :label="$t('go')">
            <el-date-picker type="date" :placeholder="$t('CD')" v-model="userForm.endTime"></el-date-picker>
        </el-form-item>
         <el-form-item>
            <el-input :placeholder="$t('pleaseEnter')" v-model="input" class="input-with-select">
              <el-select v-model="select" slot="prepend" :placeholder="$t('Pchoose')">
                 <el-option label="UID" value="uid"></el-option>
                 <!-- <el-option :label="$t('phone')" value="phoneNumber"></el-option> -->
                 <el-option :label="$t('userName')" value="personName"></el-option>
                 <!-- <el-option :label="$t('email')" value="email"></el-option>  -->
              </el-select> 
            </el-input>
        </el-form-item>
        <el-form-item>
             <el-button class="warning" v-on:click="queryGoldenWithdraw">{{$t('search')}}</el-button>
        </el-form-item>
        <el-form-item>
           <el-button class="info" v-on:click="handleReset('userForm')">{{$t('reset')}}</el-button>                 
        </el-form-item>
      </el-form>
    </el-col>

    <el-table :data="userList"   v-loading="userLoading" style="width: 100%;" :row-class-name="tableRowClassName" >
         <el-table-column prop="id"  :label="$t('gold.thead[0]')" width="160" >
          </el-table-column>
          <el-table-column prop="uid" :label="$t('gold.thead[1]')" width="160">
          </el-table-column>
          <el-table-column prop="personName"  :label="$t('gold.thead[2]')">
          </el-table-column>
          <el-table-column prop="phoneNumber"  :label="$t('gold.thead[3]')" width="155">
          </el-table-column>
          <el-table-column prop="withdrawUnit" :label="$t('gold.thead[4]')">
          </el-table-column>
          <el-table-column prop="withdrawAmount"  :label="$t('gold.thead[5]')" >
          </el-table-column>
          <el-table-column  :label="$t('gold.thead[6]')">
            <template slot-scope="scope">
              <el-button @click="checkNumber(scope.$index,scope.row)" type="text" size="small" :disabled="userList[scope.$index].status =='申请中' || userList[scope.$index].status == '提取失败'" >{{$t('CheckTheNumber')}}</el-button>
            </template>
          </el-table-column>
          <el-table-column prop="withdrawTime"  :label="$t('gold.thead[7]')" width="160"></el-table-column>
          <el-table-column prop="status"  :label="$t('gold.thead[8]')"></el-table-column>
          <el-table-column :label="$t('gold.thead[9]')">
            <template slot-scope="scope">
              
              <el-button  size="mini" @click="inputNumber(scope.$index,scope.row)" :disabled="(userList[scope.$index].status !='申请中'  || userList[scope.$index].status == '提取失败') || userList[scope.$index].withdrawTime < takeGoldTime ">{{$t('entryNumber')}}</el-button>
            </template>
          </el-table-column>
    </el-table>
  <!-- 弹框    -->
    <!-- 查看编号弹窗 -->
    <el-dialog :title="$t('CheckTheNumber')" :visible.sync="checkShow" class="checkOut" center>
      <el-form :model="form">
        <div class="check">
          <template v-for="(item,index) in checkIndex">
            <el-form-item :label="$t('serial')+ (index+1)" :key="index" :label-width="formLabelWidth">
              <el-input  auto-complete="off" v-model="lookNum[index].goldenCode" disabled></el-input>
            </el-form-item>
          </template>
        </div>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="checkShow = false" class="exit">{{$t('close')}}</el-button>
      </div>
    </el-dialog> 

    <!-- 录入编号弹窗 -->
    <el-dialog :title="$t('entryNumber')" :visible.sync="inputShow" center>
      <el-form :model="form">
        <template v-for = "(item,index) in inputIndex">
          <el-form-item :label="$t('serial')+(index+1)" :key="index" :label-width="formLabelWidth">
            <el-input v-model= 'inputArr[index]' auto-complete="off"></el-input>
          </el-form-item>
        </template>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="inputShow = false" class="exit">{{$t('cancel')}}</el-button>
        <el-button type="primary" @click="inputSure" class="sureSure">{{$t('sureEntry')}}</el-button>
      </div>
    </el-dialog>

    <el-dialog :title="$t('entryNumber')" :visible.sync="sure"  center>
      <p>{{$t('user')}}UID: <span>{{inputid}}</span>{{$t('userContent')}}</p>
      <table class="numColor">
        <tbody >
          <tr v-for = "(item,index) in inputArr" :key="index">
            <td>{{item}},</td>
          </tr>
        </tbody>
      </table>
      <p class="smallFont">{{$t('userSure')}}</p>
      <div slot="footer" class="dialog-footer">
        <el-button @click="sure = false" class="exit">{{$t('cancel')}}</el-button>
        <!-- <el-button type="primary" @click="sure = false;tiShi=true" class="sureSure">确定录入</el-button> -->
        <el-button type="primary" @click="insertGoldenCode" class="sureSure">{{$t('sureEntry')}}</el-button>
      </div>
    </el-dialog>


  <div class="tip">
    <el-dialog :title="$t('promptMessage')" :visible.sync="tiShi">
      <p>{{$t('Psure')}}</p>
      <div slot="footer" class="dialog-footer" >
        <el-button @click="tiShi = false" class="exit" >{{$t('cancel')}}</el-button>
        <el-button type="primary" @click="tiShi = false;inputShow = true" class="sureSure">{{$t('sureEntry')}}</el-button>
      </div>
    </el-dialog>
  </div>
    <div class="page" v-show="total >10">
      <el-col :span="24" class="toolbar  ">
         <el-pagination background layout="prev, pager, next" :total="total" @current-change="handleCurrentChange"></el-pagination>
      </el-col>
    </div>
  </section>
</template>
   
<script>

import {ajax,dateStr2GmtTimeStamp,getDataYear,dateStrSecond} from '../../common/js/common.js'

export default {
   data(){
     return{
      input:"",
      select:"",
      startT:'',
      endT:'',
      inputArr:[],
      lookNum:"",
      form: {
          
            },
      formLabelWidth: '60px',
      dialogFormVisible:false,
      checkShow:false,
      checkIndex:0,
      inputShow:false,
      inputIndex:0,
      sure:false,
      tiShi:false,
      inputid:null,
      inputName:null,
      userForm:{
        page:1,
        rowNum:'10',
 		    state:"",
        startTime:'',
        endTime:'',
        enterpriseName:''
       },
      userLoading:false,
      userList:[],
      goldenWithdrawDate:{
        page:1,
        rowNum:'10',
       },
      total:0,
     }
   },
   computed:{
     takeGoldTime(){
       return new Date().getTime()/1000;
     }
   },
   methods:{
    // 黄金提取记录查询
    queryGoldenWithdraw:function(){
      if(this.select != ""){
        this.userForm[this.select] = this.input;
      }
      if(this.userForm.startTime != ""){
          //  this.userForm.startTime = dateStr2GmtTimeStamp(this.userForm.startTime);
          let startTimeC = dateStr2GmtTimeStamp(this.userForm.startTime);
           this.startT = dateStrSecond( startTimeC)
      }
     if(this.userForm.endTime != ""){
      //  this.userForm.endTime = dateStr2GmtTimeStamp(this.userForm.endTime);
       let endTimeC = dateStr2GmtTimeStamp(this.userForm.endTime);
           this.endT = dateStrSecond( endTimeC)

     }
     if(this.userForm.state != ""){
        this.userForm.state = this.userForm.state;
     }
      let  data = {
          page:this.userForm.page,
          rowNum:this.userForm.rowNum,
          uid:"",
          status:this.userForm.state,
          personName:"",
          startTime:this.startT,
          endTime:this.endT,
         }
         if(this.select=="uid"){
          data.uid = this.input;
         }else if(this.select == "personName"){
          data.personName = this.input;
        }
        ajax(this,this.ApiConfig.goldenWithdraw.queryGoldenWithdraw,data,(res) => {
          if(res.code==200){
                // console.log(res)
                this.userList = res.data.rows;
                this.total =  res.data.total;
                let arr = this.userList;
                    arr.forEach((item,i,arr) => {
                       this.userList[i].ctime = getDataYear('Y-M-D h:m:s',arr[i].ctime);
                       this.userList[i].withdrawTime = getDataYear('Y-M-D h:m:s',arr[i].withdrawTime);
                        // this.userList[i].status =  this.userList[i].status == 0?"申请中":this.userList[i].status == 1?"待提取" : "已出货";
                        this.userList[i].withdrawUnit = this.userList[i].withdrawUnit==1?"100g":"1000g" ;

                          switch( this.userList[i].status)
                              {
                              case 0:
                                this.userList[i].status = "申请中"
                                break;
                              case 1:
                                this.userList[i].status = "待提货"
                                break;
                              case 2:
                                this.userList[i].status = "已出货"
                                break;
                              case 4:
                                this.userList[i].status = "提取失败"

                                break;
                              default:
                                break;
                              }



                    });

                    this.startT = "";
                    this.endT = "";
             }else{
                this.$notify({
                    title:"提示",
                    message:res.message,
                    type:"warning"
                 })
                 return false;
             }
        },"POST")
    },
    //查看编号
    checkNumber:function(index,row){
        this.checkShow = true;
        // console.log(row.withdrawAmount)
        // this.checkIndex = row.withdrawAmount;
        // console.log(row.id)
        let data = {
          id :row.id
        }
        ajax(this,this.ApiConfig.goldenWithdraw.queryGoldenCode,data,(res) =>{
          if(res.code == 200){
            console.log(res)
            
          this.checkIndex =res.data.length;
          // console.log("查看编号出现问题:")
          // console.log(row)
          //   console.log(this.checkIndex)
          this.lookNum=res.data;
          // console.log(this.lookNum)
        }else{
          this.$notify({
            title:'提示',
            message:res.message,
            type:'warning'
          })
          return false;
        }
          
        },"POST")
    },
    //点击表格中的录入编号
    inputNumber:function(index,row){
        this.inputArr=[];

        this.inputShow = true;
          this.inputid =row.id;
          this.inputName = row.personName;
        this.inputIndex =row.withdrawAmount;
        // console.log("录入的数据")
        // console.log(row)
        // console.log(this.inputIndex)
    },

    //点击录入编号中的确定
    inputSure:function(){
      /*alert(this.inputArr.length )
      alert(this.inputIndex )*/
      if(this.inputArr.length ==this.inputIndex){
        this.inputShow = false ;

        this.sure =true;
        // console.log(this.inputArr)
      }else{
        this.inputShow = false ;
        this.tiShi = true;
      }
    },
    // 录入黄金编号
    insertGoldenCode:function(){
            this.sure = false
            let  data={
              id:this.inputid,
              goldenCodes:this.inputArr,
            }

          let  setArr = new Set(this.inputArr);
          setArr = Array.from(setArr)
          // console.log('setArr',setArr)
          if(setArr.length != this.inputArr.length){
            this.$notify({
              title:"提示",
              message:"请检查输入的编号是否正确",
              type:"warning"
            })
            return false;
          }

      ajax(this,this.ApiConfig.goldenWithdraw.insertGoldenCode,data,(res) => {
        if(res.code ==200){
        // console.log(res)
        this.queryGoldenWithdraw();

      }else{
        this.$notify({
          title:"提示",
          message:res.message,
          type:"warning"
        })
        return false;
      }
      },"POST")
    },
     getUsers:function(){

     },
     handleReset:function(forName){
        this.$refs[forName].resetFields();
        this.input="";
        this.select = "";
        this.userForm.state="";
        this.userForm.startTime="";
        this.userForm.endTime="";
        this.queryGoldenWithdraw();
     },
     tableRowClassName:function({row, rowIndex}){
         if(rowIndex % 2 == 1){
            return "success-row"
         }
     },
     handleCurrentChange:function(val){
       this.userForm.page = val;
       this.queryGoldenWithdraw();
     },

   },
   mounted:function(){
    this.queryGoldenWithdraw();
   }

}
</script>
<style lang="postcss" scopted> 
      .smallFont{
        font-size: 12px;
      }
      .numColor{
        color:#bf9864;
        /* margin: 0 auto; */
      }
</style>  
<style>
#gold{
  .el-select .el-input {
    width: 105px;
  }
 .el-dialog__body{
    max-height: 280px;
    overflow: auto;
 }
 .el-dialog{
  width: 400px;

 }
 .el-dialog__title{
    color: #bf9864;
    font-size: 16px;
    height: 76px;
    vertical-align: top;
 }
 .el-form-item{
  margin-bottom: 5px;
 }
 .el-dialog--center .el-dialog__body{
  padding: 0px 25px 30px;
 }

.dialog-footer{
  .exit{
    background-color: #e7e7e7;
    width: 153px;

  }
  .sureSure{
    width: 153px;
  }
}

.checkOut{
  .el-dialog__header {
    padding: 20px 20px 0px;
}
}
.tip{
  .el-dialog{
    width: 400px;
    height: 200px;
  }
  .el-dialog__title{
    color: #979797;
    font-weight: normal;
    font-size: 14px;
    vertical-align: top;
  }
  .el-dialog__body{
    height: 50px;
    padding: 0px 20px;
    text-align: center;
    font-size: 16px;
    overflow: hidden;
    margin-top: -15px;
  }
.el-dialog__footer{
  text-align: center;
  margin-top: 15px;
}

  
}
 
}
</style>






